<template>
  <div class="app-wrapper" :class="{ hideSidebar: !sidebarOpen }">
    <Sidebar :open="sidebarOpen" @toggle-sidebar="toggleSidebar" />
    <div class="main-container">
      <Navbar :sidebar-open="sidebarOpen" @toggle-sidebar="toggleSidebar" />
      <div class="app-main">
        <el-tabs v-model="activeTab" v-if="$route.path === '/'">
          <el-tab-pane label="แข่งขันรับงาน" name="grab">
            <el-form :inline="true" class="query-form">
              <el-form-item label="ภูมิภาค">
                <el-select v-model="region" placeholder="กรุณาเลือก" size="mini">
                  <el-option label="B" value="B"></el-option>
                  <el-option label="C" value="C"></el-option>
                  <el-option label="NE" value="NE"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="ชนิดของรถ">
                <el-select v-model="vehicleType" placeholder="กรุณาเลือก" size="mini">
                  <el-option label="4W" value="4W"></el-option>
                  <el-option label="4WJ" value="4WJ"></el-option>
                  <el-option label="6W7.2" value="6W7.2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button 
                  type="primary" 
                  size="mini" 
                  @click="handleSearch"
                  :class="{ 'button-clicked': isSearching }"
                >ค้นหา</el-button>
              </el-form-item>
            </el-form>
            <JobTable 
              :tableData="paginatedTableData"
              @open-dialog="openDialog"
            >
              <el-table-column prop="route" label="เลขเส้นทาง" class-name="route-column" />
              <el-table-column prop="region" label="ภูมิภาค" class-name="region-column" />
              <el-table-column prop="vehicleType" label="ชนิดของรถ" class-name="vehicle-column" />
              <el-table-column prop="distance" label="ระยะทาง" class-name="distance-column" />
              <el-table-column prop="endTime" label="เวลาสิ้นสุด" class-name="time-column" />
              <el-table-column prop="remainingTime" label="เวลาที่เหลือ" class-name="remaining-column" />
              <el-table-column prop="expectedArrival" label="เวลาถึง" class-name="arrival-column" />
              <el-table-column prop="fare" label="ค่าโดยสาร" class-name="fare-column" />
            </JobTable>
            
            <!-- Add pagination and export button -->
            <div class="pagination-container">
              <el-pagination
                @current-change="handlePageChange"
                :current-page.sync="currentPage"
                :page-size="pageSize"
                layout="prev, pager, next"
                :total="tableData.length">
              </el-pagination>
              <el-button 
                type="success" 
                size="small" 
                @click="saveRoutesToJson"
                class="export-button"
              >
                <i class="el-icon-download"></i>
                บันทึก JSON
              </el-button>
            </div>

            <el-dialog
              title="ยืนยันแข่งขันรับงาน"
              :visible.sync="dialogVisible"
              width="30%"
              @open="initTurnstile"
              @close="destroyTurnstile"
            >
              <p>เส้นทาง: {{ selectedRow.route }}</p>
              <p>ภูมิภาค: {{ selectedRow.region }}</p>
              <p>ชนิดของรถ: {{ selectedRow.vehicleType }}</p>

              <!-- Cloudflare Turnstile Widget -->
              <div id="turnstile-container"></div>

              <span slot="footer" class="dialog-footer">
                <el-button
                  type="primary"
                  :disabled="!isCaptchaSolved"
                  @click="goToDetails"
                  >แข่งขันรับงาน</el-button
                >
              </span>
            </el-dialog>
          </el-tab-pane>
          <el-tab-pane label="รายการของฉัน" name="my-list">
            <p>ยังไม่มีข้อมูล</p>
          </el-tab-pane>
        </el-tabs>
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar.vue";
import Navbar from "@/components/Navbar.vue";
import JobTable from "@/components/JobTable.vue";
import { Notification as ElNotification } from "element-ui";
import routeData from "@/route.json";

export default {
  components: { Sidebar, Navbar, JobTable },
  data() {
    return {
      sidebarOpen: true,
      activeTab: "grab",
      region: "",
      vehicleType: "",
      tableData: [],
      dialogVisible: false,
      selectedRow: {},
      turnstileWidgetId: null,
      isCaptchaSolved: false,
      isSearching: false,
      currentPage: 1,
      pageSize: 8,
    };
  },
  computed: {
    paginatedTableData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.tableData.slice(start, end);
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    openDialog(row) {
      this.selectedRow = row;
      this.dialogVisible = true;
    },
    initTurnstile() {
      const script = document.createElement("script");
      script.src = "https://challenges.cloudflare.com/turnstile/v0/api.js";
      script.async = true;
      script.defer = true;
      document.head.appendChild(script);

      script.onload = () => {
        if (window.turnstile) {
          this.turnstileWidgetId = window.turnstile.render(
            "#turnstile-container",
            {
              sitekey: "0x4AAAAAAA7SN9BgICU6k8R5",
              callback: (token) => {
                this.isCaptchaSolved = true;
              },
            }
          );
        }
      };
    },
    destroyTurnstile() {
      if (this.turnstileWidgetId && window.turnstile) {
        window.turnstile.remove(this.turnstileWidgetId);
        this.turnstileWidgetId = null;
        this.isCaptchaSolved = false;
      }
    },
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
    },
    handleSearch() {
      this.isSearching = true;
      this.currentPage = 1;
      this.fetchData();
      setTimeout(() => {
        this.isSearching = false;
      }, 500);
    },
    handlePageChange(page) {
      this.currentPage = page;
    },
    fetchData() {
      console.log("ค้นหาด้วย:", this.region, this.vehicleType);
      // กรองข้อมูลตามภูมิภาคและประเภทรถ
      this.tableData = routeData.routes.filter(route => {
        if (this.region && this.vehicleType) {
          return route.region === this.region && route.vehicleType === this.vehicleType;
        } else if (this.region) {
          return route.region === this.region;
        } else if (this.vehicleType) {
          return route.vehicleType === this.vehicleType;
        }
        return true;
      });
    },
    saveRoutesToJson() {
      const routeExport = {
        timestamp: new Date().toISOString(),
        routes: this.tableData
      };

      const blob = new Blob([JSON.stringify(routeExport, null, 2)], { type: 'application/json' });
      const url = window.URL.createObjectURL(blob);
      
      const a = document.createElement('a');
      a.href = url;
      a.download = `routes_${new Date().toISOString().slice(0,19).replace(/[:]/g, '-')}.json`;
      document.body.appendChild(a);
      a.click();
      
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);

      ElNotification({
        title: "สำเร็จ",
        message: "บันทึกข้อมูลเรียบร้อยแล้ว",
        type: "success",
        position: "bottom-right"
      });
    },
    goToDetails() {
      if (window.turnstile) {
        const token = window.turnstile.getResponse(this.turnstileWidgetId);
        if (token) {
          ElNotification({
            title: "สำเร็จ",
            message: "ยืนยันการแข่งขันรับงานสำเร็จ",
            type: "success",
          });
          this.dialogVisible = false;
          this.$router.push({
            name: "Details",
            params: {
              route: this.selectedRow.route,
              region: this.selectedRow.region,
              vehicleType: this.selectedRow.vehicleType
            },
          });
        } else {
          ElNotification({
            title: "คำเตือน",
            message: "กรุณาทำ turnstile ให้สำเร็จ",
            type: "warning",
          });
        }
      }
    },
  },
};
</script>

