import Vue from 'vue';
import Router from 'vue-router';
import Dashboard from '@/views/Dashboard.vue';
import Home from '@/views/Home.vue'; // เพิ่มการ import Home.vue
import Details from '@/views/Details.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
    },
    {
      path: '/details/:id',
      name: 'Details',
      component: Details,
      props: true,
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
});
